.authView{
display:flex;
justify-content: center;
margin-top:40px

}

.label {
    margin-bottom: 20px;
    font-weight: 500;
}

.input {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    border: 2px solid var(--light-accent-color);
    width: 300px;
    background-color: var(--secondary-background-color)
}

.input:active,
.input:focus {
    outline: none;
    border: 3px solid var(--light-accent-color);
    background-color: var(--primary-background-color)
}


.button {
    margin-top: 20px;
    padding: 5px;
    width: 150px;
    border: 2px solid var(--light-accent-color);
    border-radius: 3px;
    color: var(--primary-text-color);
    background-color: var(--secondary-background-color);
    font-weight: 600;
    text-transform: uppercase;
    transition: transform 200ms linear, font-weight 200ms linear;
}

/* .button:active {
    box-shadow: 0 5px #666;
} */

.button:hover,
.button:focus {
    opacity: 1;
    font-weight: 700;
    transform: scale(1.1);
}